@import url(https://fonts.googleapis.com/css2?family=Exo+2&family=Oswald:wght@400;600&display=swap);
.repo__card {
  list-style-type: none;
  margin-bottom: 25px;
  background-color: #495155;
  width: 100%;
  padding: 15px;
  border-radius: 15px;
}

.repo__description {
  width: 90%;
  margin: 10px 0px;
}

.repo__made-with {
  margin-top: 5px;
  margin-bottom: 10px;
}

.repo__made-with span {
  font-family: 'Oswald', sans-serif;
  font-weight: 600;
}

.repo__stars-link {
  display: flex;
}

.repo__stars-link svg {
  margin-right: 5px;
  position: relative;
  top: 3px;
}

.repo__stars-link p:first-child {
  margin-right: 20px;
}

.repo__stars-link a {
  color: #fff;
  text-decoration: none;
}

@media (min-width: 768px) {
  .repo__card {
    margin-bottom: 40px;
  }
}

.profile {
  width: 100%;
  text-align: center;
  background-color: #495155;
  padding: 30px 0px;
  border-radius: 15px;
}

.profile__image {
  margin: 0 auto;
  width: 200px;
}

.profile__basic-info {
  margin: 15px 0;
}

.profile__basic-info a {
  color: #ffffff;
  text-decoration: none;
}

.profile__basic-info a:hover {
  border-bottom: 1px solid #ffffff;
}

.profile__image img {
  width: 100%;
  border-radius: 50%;
}

.profile__name {
  margin: 10px 0px;
}

.profile__icons {
  font-size: 20px;
  position: relative;
  top: 3px;
  margin-right: 5px;
}

.profile__bio {
  width: 90%;
  margin: 30px auto;
  text-align: center;
}

.profile__follow-info {
  display: flex;
  width: 80%;
  margin: 0 auto;
  justify-content: space-between;
  text-transform: uppercase;
}

.profile__online-presence {
  width: 100px;
  margin: 0 auto;
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}

.profile__online-presence a {
  color: #ffffff;
  font-size: 20px;
}

@media (min-width: 768px) {
  .profile {
    width: 35%;
    margin-right: 30px;
    /* position: fixed; */
  }

  .profile__online-presence {
    margin-top: 50px;
    margin-bottom: 30px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #27363d;
  color: #ffffff;
  font-family: 'Exo 2', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Oswald', sans-serif;
}

.container {
  max-width: 1170px;
  padding: 7.5px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.repo-page {
  width: 100%;
  margin-top: 30px;
  margin-bottom: 25px;
}

.repo-page h1 {
  margin-bottom: 25px;
}

@media (min-width: 480px) {
  .container {
    padding: 15px;
  }
}

@media (min-width: 768px) {
  .container {
    padding-top: 50px;
    flex-direction: row;
  }

  .repo-page {
    margin-top: 0px;
  }
}

